.device {
	display: flex;
	flex-direction: column;
	gap: 10px;
	&__image {
		display: flex;
		justify-content: center;
		height: 120px;
		position: relative;
		img {
			height: 100%;
			max-width: 100%;
			margin: 0 auto;
		}
		&:hover {
			.device__hover {
				opacity: 1;
				visibility: visible;
			}
		}
	}
	&__hover {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		opacity: 0;
		visibility: hidden;
	}
	&__zoom {
		align-self: flex-end;
	}
	&__name {
		flex: 1;
		a {
			font-size: 12px;
			line-height: 120%;
			color: $tc1;
		}
		div {
			font-size: 10px;
			line-height: 120%;
			font-weight: 700;
			color: $tc2;
			margin-top: 5px;
		}
	}
	&__icons {
		display: flex;
		gap: 8px;
	}
	&__code {
		font-size: 12px;
		span {
			color: $cRed;
			font-weight: 700;
			margin-left: 10px;
		}
	}
	&__price {
		font-size: 15px;
		font-weight: 700;
		a {
			font-size: 12px;
			font-weight: 400;
			color: #828282;
		}
	}
	&__status {
		font-size: 12px;
	}
	&__order {
		form {
			height: 25px;
			display: flex;
			gap: 8px;
		}
	}
	&__add {
		a {
			font-size: 12px;
			color: $tc2;
		}
	}
}
.status {
	&__na {
		color: $cRed;
	}
	&__wait {
		color: $primary-color;
	}
}





.device_row {
	border-bottom: 1px solid #c4c4c4;
	td {
		padding: 10px;
		padding-right: 20px;
		font-size: 14px;
		vertical-align: top;
		&:last-child {
			padding-right: 0;
		}
	}
	.device {
		&__image {
			width: 120px;
			height: 100px;
		}
		&__hover {
			padding-top: 10px;
		}
		&__name {
			font-size: 14px;
		}
		&__price {
			font-size: 12px;
			white-space: nowrap;
			div {
				color: $cRed;
				font-weight: 400;
				font-size: 12px;
				margin-top: 5px;
			}
		}
		&__status {
			.qty {
				white-space: nowrap;
			}
			.status__na,
			.status__wait {
				font-size: 12px;
				margin-bottom: 10px;
			}
			a {
				color: #828282;
				font-size: 12px;
				font-weight: 400;
			}
		}
		&__order {
			margin-bottom: 10px;
			a.button {
				img {
					max-width: unset;
					max-height: unset;
				}
			}
		}
		&__info {
			> * {
				margin-bottom: 5px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
		&__icons {
			margin-top: 10px;
		}
	}
}





.device__info {
	.params {
		tr {
			display: block;
			border: none;
		}
		td {
			font-size: 12px;
			display: inline-block;
			padding: 2px;
			font-weight: 400;
			&:first-child {
				color: $gray-2;
			}
		}
	}
}













//============= slick =================

.device {
	min-width: 0;
}

.device-nav {
	padding-top: $gap-sm;
	.slick-dots {
		display: flex;
		justify-content: center;
		align-items: center;
		li {
			margin-right: 8px;
			font-size: 0;
			button {
				font-size: 0;
				width: 11px;
				height: 11px;
				border-radius: 50%;
				background-color: $gray-2;
			}
			&:last-child {
				margin-right: 0;
			}
		}
		.slick-active {
			button {
				background-color: $primary-color;
			}
		}
	}

}
