.devices_section {
	margin-bottom: $gap-lg;
}
.devices_product {
	.devices__wrap {
		grid-template-columns: repeat(4, 1fr);
	}
}
.devices__wrap {
	display: grid;
	grid-template-columns: repeat(6, 1fr);
	grid-gap: $gap-md;
	@media (max-width: $bp2) {
		grid-template-columns: repeat(5, 1fr);
	}
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
	}
}
.show-d {
	.devices__wrap {
		grid-template-columns: repeat(5, 1fr);
		@media (max-width: $bp2) {
			grid-template-columns: repeat(4, 1fr);
		}
	}
}
.show-t {
	.sections__wrap {
		grid-template-columns: repeat(2, 1fr);
	}
}
.devices__wrap_rows {
	tr:first-child {
		font-size: 11px;
		line-height: 1.2;
		font-weight: 700;
		background-color: #e0e0e0;
		td {
			padding: 10px;
			vertical-align: top;
		}
	}
}
