.slider-container {
	position: relative;
	@media (max-width: $bp4) {
		display: none;
	}
}
.slider {
	display: flex;
	height: 251px;
	background: linear-gradient(92.33deg, #E7EFEB 1.45%, #BED9CC 99.91%);
	border-radius: 4px;
	position: relative;
	margin-bottom: 40px;
	&__item {
		width: 100%;
		position: relative;
	}
	&__image {
		position: absolute;
		top: 50%;
		right: 50px;
		transform: translate(0, -50%);
	}
	&__content {
		padding: 35px 0 35px 50px;
		margin-right: 50%;
	}
	&__title {
		font-size: 30px;
		line-height: 1.2;
		font-weight: 700;
		margin-bottom: 15px;
	}
	&__text {
		font-size: 20px;
		line-height: 1.2;
		font-weight: 300;
	}
	// &__nav {
	// 	position: absolute;
	// 	bottom: 20px;
	// 	left: 50px;
	// }
	.slick-list {
		overflow: hidden;
		display: flex;
		width: 100%;
	}
	.slick-track {
		display: flex;
	}
}
.slider-nav {
	position: absolute;
	bottom: 20px;
	left: 50px;
	z-index: 1000;
	.slick-dots {
		display: flex;
		justify-content: center;
		align-items: center;
		li {
			margin-right: 7.5px;
			font-size: 0;
			button {
				font-size: 0;
				width: 10.5px;
				height: 10.5px;
				border-radius: 50%;
				background-color: $gray-2;
			}
			&:last-child {
				margin-right: 0;
			}
		}
		.slick-active {
			button {
				background-color: $primary-color;
			}
		}
	}
}
