.popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $overlay-color;
	z-index: 1000;
	&__body {
		height: 100%;
		position: relative;
	}
	&__content {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #fff;
		overflow: auto;
		padding: 20px;
		transition: transform, 500ms;
		&_next {
			transform: translateX(100%);
		}
	}
	&__header {
		display: flex;
		margin-bottom: 20px;
	}
	&__title {
		font-size: 24px;
		font-weight: bold;
		flex: 1;
	}
	&__close {
		display: flex;
		flex: 0 0 24px;
		align-items: flex-start;
		justify-content: center;
	}
	&__back {
		display: flex;
		flex: 0 0 30px;
		justify-content: flex-start;
		align-items: flex-start;
	}
	&_basic {
		.popup__content {
			position: static;
			margin: auto;
		}
	}
	table {
		border-collapse: collapse;
		td {
			border: 1px solid #e0e0e0;
			padding: 5px 8px;
		}
	}
}
.popup-menu {
	margin: 0 -20px;
	&__item {
		padding: 20px;
		display: flex;
		border-bottom: 1px solid #E9EDEE;
	}
	&__link {
		flex: 1;
		font-size: 20px;
		line-height: 120%;
	}
	&__arrow {
		flex: 0 0 24px;
		align-items: center;
		justify-content: flex-end;
	}
}
@media (min-width: $bp4 + 1px) {
	.popup {
		&__body {
			display: flex;
			align-items: center;
			justify-content: center;
		}
		&__content {
			height: unset;
			max-height: 600px;
			max-width: 600px;
		}
	}
}
.search-mobile {
	form {
		margin-bottom: 20px;
		> div:first-child {
			display: flex;
			height: 36px;
			position: relative;
			margin-bottom: 10px;
		}
	}
	input[type="text"] {
		border: 1px solid $primary-color;
		padding: 0 10px;
		font-size: 14px;
		flex: 1;
	}
	&__reset {
		position: absolute;
		top: 7px;
		right: 70px;
		padding: 5px;
		font-size: 0;
	}
	.button {
		border-radius: 0 2px 2px 0;
		font-size: 14px;
		font-weight: 700;
		padding: 10px;
	}
	&__option {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.suggest {
		visibility: visible;
		position: static;
		padding: unset;
	}
}
.search-option {
	font-size: 14px;
	display: flex;
	align-items: center;
}
