@import 'basic/import';


.partners {
  display: flex;
  flex-direction: column;
  gap: 10px;
  @media (max-width: $bp4) {
    display: none;
  }
  @media (max-width: $bp2) {
    margin-bottom: 40px;
  }

}
.content__main {
  .partners {
    flex-direction: row;
    @media (min-width: $bp2) {
      display: none;
    }
  }
}
.partner-banner {
  padding: 10px;
  background: #FFFFFF;
  box-shadow: 0px 4px 10px rgba(0, 63, 29, 0.15);
  border-radius: 2px;
  img {
    max-width: 100%;
  }
}


@media (max-width: $bp3) {
  .show-t {
    display: block;
  }
  .show-d {
    display: none;
  }
}
@media (min-width: $bp3) {
  .show-t {
    display: none;
  }
  .show-d {
    display: block;
  }
}
@media (max-width: $bp4) {
  .show_m {
    display: block;
  }
  .hide_m {
    display: none;
  }
}
@media (min-width: $bp4 + 1px) {
  .show_m {
    display: none;
  }
  .hide_m {
    display: block;
  }
}
