body {
	font-family: $ff;
	font-size: $fz;
	color: $tc1;
	background-color: #f2f2f2;
	@media (max-width: $bp3){
		background-color: #fff;
	}
}
a {
	color: $link-color;
	text-decoration: underline;
	&:hover {
		text-decoration: none;
	}
}

.icon {
	display: flex;
	height: 16px;
	width: 24px;
	display: flex;
	margin-right: 4px;
	img {
		max-height: 100%;
		margin: auto;
	}
}
.icon_size_sm {
	width: 16px;
	height: 16px;
}
.icon_size_lg {
	width: 32px;
	height: 32px;
}

.icon-link {
	display: inline-flex;
	align-items: center;
	font-size: 14px;
}
.icon-link_after {
	.icon {
		margin: 0 0 0 4px;
	}
}

.ptb {
	padding-top: $gap-xxl;
	padding-bottom: $gap-xxl;
}
.pt {
	padding-top: $gap-xxl;
}
.pb {
	padding-bottom: $gap-xxl;
}


.divider {
	height: 4px;
	width: 100px;
	background-color: #E6D0B0; // var
	margin: 0 auto;
}

.img {
	position: relative;
	&:before {
		content: "";
		display: block;
		padding-top: 100%;
	}
	img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.card {
	border: 1px solid $gray-3;
	padding: 16px;
	background-color: #fff;
}

.device-carousel-colors {
	.card {
		padding: 0;
		border: none;
		text-align: left;
		width: 100%;
		.device__image {
			margin: 0;
			margin-bottom: 8px;
		}
	}
}


.tag {
	padding: $tag-padding;
	font-size: $tag-fz;
	color: #fff; // var tag-color
	background-color: $tag-background-color;
}


//======== container =======================================================================
.container {
	margin: 0 auto;
	@media (min-width: $bp1) {
		width: $cw1;
	}
	@media (max-width: $bp1 - 1) {
		width: $cw2;
		padding: 0 16px;
	}
}
//======== container end =======================================================================
img {
	max-width: 100%;
	max-height: 100%;
}


.text p{
	line-height: 1.3;
	color: #333;
}




.content__wrap {
	display: flex;
	gap: 15px;
	> div {
		padding-top: 10px;
		padding-bottom: 10px;
	}
	.content__main {
		padding-bottom: 50px;
		@media (max-width: $bp3) {
			padding-right: 0;
			padding-left: 0;
		}
	}
	@media(max-width: $bp3) {
		flex-direction: column;
	}
}
.content__side {
	flex: 0 0 200px;
	width: 200px; // slick-slider needs width in px
	@media (max-width: $bp3) {
		display: none;
	}
	> * {
		margin-bottom: $gap-xl;
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.content__main {
	flex: 1 1 auto;
	min-width: 0px; // fix for slick-slider
	background-color: #fff;
	padding: 10px;
}
.content__side-2 {
	flex: 0 0 178px;
	width: 178px; //slick-slider needs width in px
	display: flex;
	flex-direction: column;
	gap: 20px;
	@media (max-width: $bp2) {
		display: none;
	}
}



//======================= side info
.side-info {
	font-size: 14px;
	p {
		margin-bottom: $gap-sm;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.title {
		font-size: 20px;
	}
}
.side-button {
	button {
		font-size: 15px;
	}
}
//======================= side info end


//======================= side links
.side-links__link {
	display: block;
	margin-bottom: $gap-md;
	font-size: 14px;
	line-height: 1.2;
	&:last-child {
		margin-bottom: 0;
	}
}
//======================= side links end



.text {
	margin-bottom: $gap-lg;
	> * {
		margin-bottom: $gap-md;
	}
}
.date {
	font-size: 13px;
	color: $gray-1;
}
.date_title {
	position: relative;
	top: -$gap-md;
}




.compare-items {
	max-width: 100%;
	overflow: hidden;
	overflow-x: auto;
	display: flex;
	// margin: 0 -$gap-sm;
	padding-bottom: $gap-md;
}
.compare-items__item {
	flex: 0 0 25%;
	// padding: 0 $gap-sm;
	padding-right: 2%;
	overflow: hidden;
}
.compare-items__params {
	margin-top: $gap-md;
	li {
		font-size: 13px;
		display: flex;
		flex-direction: column;
		margin-bottom: $gap-sm;
		&:last-child {
			margin-bottom: 0;
		}
		span {
			&:first-child {
				color: $gray-2;
			}
		}
	}
}





// orders history
.orders-history {
	display: table;
	width: 100%;
}
.past-order {
	display: table-row;
	&:first-child {
		> * {
			padding-top: 0;
		}
	}
	> * {
		display: table-cell;
		font-size: 14px;
		white-space: nowrap;
		padding: 7px 20px 7px 0;
		border-bottom: 1px solid #e0e0e0;
		&:last-child {
			padding-right: 0;
		}
	}
	&__link {
		a {
			color: #333;
			font-weight: 700;
		}
	}
	&__date {
		color: #828282;
		width: 100%;
	}
	&__price {
		font-weight: 700;
	}
	&__delete {
		img {
			max-width: unset;
		}
	}
	&__button {
		.button {
			&:before {
				content: 'Копировать заказ в корзину';
			}
		}
	}
}
@media (max-width: $bp4) {
	.orders-history {
		display: block;
	}
	.past-order {
		display: grid;
		width: 100%;
		grid-template-columns: auto 1fr 35px 12px;
		grid-gap: 5px 10px;
		margin-bottom: 20px;
		> * {
			margin: 0;
			padding: 0;
			border: none;
			display: block;
		}
		&__link {
		}
		&__date {
			order: 1;
		}
		&__price {
			grid-row: span 2;
			text-align: right;
		}
		&__button {
			grid-row: span 2;
			.button {
				&:before {
					content: url(../img/icon-button-cart.png);
				}
			}
		}
		&__delete {
			grid-row: span 2;
		}
	}
}
.past-order-info {
	display: flex;
	align-items: center;
	margin-top: -30px;
	margin-bottom: 30px;
	div {
		flex: 1;
	}
	@media (max-width: $bp4) {
		margin-top: 0px;
	}
	.button:before {
		content: 'Копировать заказ в корзину';
		@media (max-width: $bp4) {
			content: 'Копировать в корзину';
		}
	}
}
// orders history end




// info-section
.info-items_section {
	margin-bottom: $gap-lg;
}
.info-items__wrap {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: $gap-md;
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
	}
	@media (max-width: $bp5) {
		grid-template-columns: repeat(1, 1fr);
	}
}
.info-item {
	position: relative;
	overflow: hidden;
}
.info-item__image {
	margin: -16px -16px $gap-md;
	&.img:before {
		padding-top: 60%;
	}
}
.info-item__info {
		display: flex;
		flex-direction: column;
	> * {
		margin-bottom: $gap-sm;
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.info-item__date {
	font-size: 12px;
	color: $gray-2;
}
.info-item__text {
	font-size: 13px;
	color: $gray-1;
	line-height: 1.3;
}
.info-item__link {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
// info-section end



.section-text {
	margin-bottom: $gap-md;
	line-height: 1.3;
}



.alert {
	font-size: 14px;
	border-radius: 4px;
	padding: $gap-sm $gap-md;
	margin-bottom: $gap-lg;
	display: flex;
}
.alert_danger {
	background-color: #fff1c8;
	color: #8e7425;
}
.alert_success {
	background-color: #DCEDC8;
    color: #486a20;
}
.alert__title {
	margin-right: $gap-sm;
}



.search-links {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-bottom: 10px;
	&__item {
		margin-right: 10px;
		margin-bottom: 10px;
	}
}
.search-link {
	display: flex;
	align-items: center;
	font-size: 12px;
	color: #333;
	a {
		color: inherit;
		margin-right: 3px;
	}
	&_active {
		font-weight: 700;
		a, span {
			color: $primary-color;
		}
	}
}
