$tc1: #333; //to var
$tc2: #828282; //to var

.top-info {
	border-top: 4px solid $primary-color;
	padding: 20px 0 0;
	background-color: #fff;
	@media (max-width: $bp3) {
		display: none;
	}
}
.top-info__wrap {
	display: flex;
	gap: 30px;
}
.top-info__location {
	padding-left: 30px;
	background-image: url(../img/icon-map.png);
	background-repeat: no-repeat;
	background-position: top left;
	display: flex;
	flex-direction: column;
	gap: 2px;
	p {
		flex-basis: 100%;
		font-size: 16px;
	}
	>div {
		flex-basis: 100%;
		font-size: 11px;
		display: flex;
		justify-content: space-between;
	}
	a {
		text-decoration: none;
	}
}
.top-info__phones {
	display: flex;
	justify-content: space-between;
	flex: 1;
}
.phones__item {
	display: flex;
	flex-direction: column;
	gap: 2px;
}
.phones__number {
	font-size: 14px;
}
.phones__description {
	font-size: 12px;
	color: $tc2;
	text-align: right;
}
