.header {
	@media (max-width: $bp3) {
		display: none;
	}
	padding: 22px 0 18px;
	background-color: #fff;
}
.header__wrap {
	display: flex;
	gap: 35px;
	align-items: center;
}

.logo {
	align-self: flex-start;
	flex-basis: 210px;
	display: flex;
	flex-direction: column;
	@media (max-width: $bp3) {
		height: 32px;
	}
	&__hours {
		margin-top: 3px;
	}
}
.logo__image {
	max-height: 100%;
}

.work-hours {
	display: flex;
	flex-direction: column;
	gap: 2px;
	p {
		display: flex;
		font-weight: bold;
		font-size: 14px;
		justify-content: center;
		span {
			margin-left: 10px;
		}
	}
}
.header-mobile {
	@media (min-width: $bp3 + 1px) {
		display: none;
	}
	&__top {
		display: flex;
		align-items: center;
		padding: 15px;
		border-top: 4px solid $primary-color;
		background-color: #fff;
	}
	&__logo {
		height: 32px;
	}
	&__nav {
		display: flex;
		align-items: center;
		background-color: #e0e0e0;
		height: 75px;
		padding: 15px;
	}
	&__menu {
		margin-right: 30px;
		.button {
			height: 42px;
			padding: 10px;
			font-size: 20px;
			font-weight: 700;
		}
	}
	&__small-icons {
		display: flex;
		margin-left: auto;
	}
	&__small-icon {
		margin-right: 20px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__icons {
		flex: 1;
		display: flex;
		justify-content: space-between;
	}
	&__icon {
		&:last-child {
			margin-right: 0;
		}
	}
}
@media (min-width: $bp4 + 1px) {
	.header-mobile {
		&__icons {
			flex: unset;
			margin-left: auto;
		}
		&__icon {
			margin-right: 30px;
		}
	}
}
.mobile-small-icon {
	position: relative;
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
.mobile-icon {
	font-size: 12px;
	position: relative;
	text-align: center;
	&__icon {
		margin-bottom: 5px;
	}
	&__qty {
		color: #fff;
		background-color: $cRed;
		position: absolute;
		top: 0;
		left: calc(50% + 12px);
		transform: translate(-50%, -5px);
		padding: 3px 5px;
		border-radius: 50px;
	}
	&__label {
		color: #828282;
	}
}
