.main-menu {
	background-color: #e0e0e0;
	@media (max-width: $bp3) {
		display: none;
	}
}
.main-menu__wrap {
	display: flex;
	align-items: center;
}
.main-menu__item {
	flex: 1 1 auto;
	&:hover {
		background-color: rgba(0,0,0,.05);
	}
	&.active {
		@extend .main-menu__item:hover;
		a {
			font-weight: 700;
		}
	}
	> a {
		color: $tc1;
		display: flex;
		height: 54px;
		justify-content: center;
		align-items: center;
		font-size: 20px;
		text-decoration: none;
	}
}
