//pagination
$pagination-active-bgr: $secondary-color;
$pagination-fz: 14px;





.pagination {
	display: flex;
	align-items: baseline;
	margin-top: 30px;
}
.pagination__label {
	margin-right: 10px;
}
.pagination__wrap {
	display: flex;
	flex-wrap: wrap;
}
.pagination__item {
	height: 24px;
	padding: 0 10px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #bdbdbd;
	font-size: 14px;
	margin-right: 5px;
	border-radius: 2px;
	position: relative;
	&:last-child {
		margin-right: 0;
	}
	a {
		color: $tc1;
		text-decoration: none;
	}
}
.pagination__item_active {
	color: #fff;
	background-color: $primary-color;
	&:after {
		content: url(../img/icon-page-caret-down.png);
		display: block;
		margin-left: 5px;
	}
	&:hover {
		.pag-dd {
			display: block;
		}
	}
}
.pagination__dots {
	margin-right: 5px;
	align-self: flex-end;
}
.pag-dd {
	display: none;
	position: absolute;
	top: 100%;
	left: 50%;
	transform: translateX(-50%);
	z-index: 1;
	padding-top: 10px;
	&__item {
		padding: 5px 10px;
		display: block;
		color: $tc1;
		text-decoration: none;
		&:hover {
			background-color: #e0e0e0;
		}
	}
	&__container {
		background-color: #fff;
		box-shadow: 0 0 15px gray;
		display: flex;
		flex-direction: column;
		border-radius: 2px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 50%;
			transform: translate(-50%, -100%);
			border: 5px solid transparent;
			border-bottom-color: #fff;
		}
	}
}
