.best-price {
  background-color: #fff;
  &__header {
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 10px;
    background-color: #e0e0e0;
    font-size: 21px;
    font-weight: 700;
    a {
      font-size: 12px;
      font-weight: 400;
      color: $tc2;
    }
  }
  &__item {
    min-width: 0;
    display: flex;
  }
  &__slider {
    padding: 10px;
  }
}
@media (min-width: $bp2 + 1px) {
  .content__side {
    .best-price {
      display: none;
    }
  }
}
.slick-track {
	display: flex;
}
.slick-list {
	overflow: hidden;
}
