.header-icons {
	display: flex;
	justify-content: space-between;
	gap: 30px;
}
.header-icon {
	display: flex;
	gap: 5px;
	flex-direction: column;
	text-align: center;
	position: relative;
	padding-bottom: 10px;
	margin-bottom: -10px;
	&:nth-child(3) {
		.header-icon__counter {
			background-color: $primary-color;
		}
	}
	&__icon {
		position: relative;
	}
	&__info {
		font-size: 14px;
		display: flex;
		gap: 3px;
		span {
			font-weight: 700;
		}
		a {
			color: $cBlue;
			text-decoration: none;
		}
	}
	&__counter {
		position: absolute;
		top: -10px;
		left: calc(50% + 15px);
		transform: translate(-50%, 0);
		border-radius: 10px;
		display: block;
		padding: 5px 7px;
		background-color: $cRed;
		color: #fff;
		font-size: 12px;
	}
	&__dd {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__dropdown {
		top: calc(100% - 1px);
	}
	&:hover {
		.header-icon__dropdown,
		.dd-cart {
			display: flex;
		}
	}
}
@media (max-width: $bp2) {
		.header-icons {
			display: grid;
			grid-template: auto auto / auto auto;
			grid-gap: 10px;
		}
		.header-icon {
			flex-direction: row;
			align-items: center;
			&:nth-child(4) {
				display: none;
			}
		}
	}
.dropdown {
	display: none;
	flex-direction: column;
	gap: 1px;
	background-color: #fff;
	box-shadow: 0px 0px 15px #00000055;
	border-radius: 2px;
	position: absolute;
	left: 50%;
	transform: translate(-50%, 0);
	z-index: 1001;
	&__item {
		padding: 10px;
		font-size: 14px;
		color: $tc1;
		text-decoration: none;
		text-align: left;
		white-space: nowrap;
		&:hover {
			background-color: #f2f2f2;
		}
		&.active {
			font-weight: 700;
		}
	}
	&:before {
		content: '';
		display: block;
		border: 6px solid transparent;
		border-bottom-color: #fff;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-50%, -100%);
	}
	&_last {
		left: unset;
		right: 0;
		transform: unset;
		&:before {
			left: calc(100% - 20px);
		}
	}
}
.dd-cart {
	display: none;
	flex-direction: column;
	gap: 20px;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	top: calc(100% - 1px);
	padding: 20px;
	z-index: 1001;
	background-color: #fff;
	border-radius: 2px;
	box-shadow: 0px 0px 15px #00000055;
	font-size: 14px;
	width: 300px;
	&:before {
		content: '';
		display: block;
		border: 6px solid transparent;
		border-bottom-color: #fff;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-50%, -100%);
	}
	&__scroll {
		display: flex;
		flex-direction: column;
		gap: 10px;
		height: 200px;
		overflow-y: auto;
		padding-right: 20px;
		margin-right: -20px;
		&::-webkit-scrollbar {
		  width: 4px;
		}
		&::-webkit-scrollbar-track {
		  background: #f1f1f1;
		}
		&::-webkit-scrollbar-thumb {
		  background: #888;
		}
		&::-webkit-scrollbar-thumb:hover {
		  background: #555;
		}
	}
	&__item {
		display: flex;
		gap: 10px;
		text-align: left;
	}
	&__image {
		flex: 0 0 50px;
	}
	&__info {
		flex: 1;
		display: flex;
		flex-direction: column;
		gap: 4px;
	}
	&__name {
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
	&__code {
		color: $tc2;
	}
	&__close {
		flex: 0 0 auto;
	}
	&__button {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 42px;
		font-size: 18px;
		text-decoration: none;
		background-color: $primary-color;
		color: #fff;
		border-radius: 2px;
	}
}
