.side-menu {
	&__links {
		display: flex;
		gap: 12px;
		flex-direction: column;
	}
	&__header {
		margin: 20px 0 24px;

	}
	&__title {
		font-size: 20px;
		font-weight: 700;
		text-decoration: none;
		color: #000;
	}
	&__update {
		display: inline-block;
		padding: 5px 7px;
		border-radius: 10px;
		font-size: 12px;
		color: #fff;
		background-color: $primary-color;
		align-self: flex-start;
		margin-top: 6px;
	}
	&__item {
		display: flex;
		gap: 10px;
		&_active {
			.side-menu__link {
				font-weight: 700;
			}
		}
	}
	&__toggle {
		flex: 0 0 auto;
		padding-top: 2px;
	}
	&__link {
		font-size: 13px;
		line-height: 1.2;
		color: $tc1;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
		&_active {
			font-weight: 700;
		}
	}
	@media(max-width: $bp3) {
		display: none;
	}
}

.side-menu__item_level_2 {
	margin-left: 20px;
}
.side-menu__item_level_3 {
	margin-left: 40px;
}
