.header-search {
	padding: 10px;
	background-color: #e0e0e0;
	flex: 1;
	form {
    position: relative;
		display: flex;
		flex-direction: column;
		gap: 5px;
		>div:first-child {
			display: flex;
			position: relative;
		}
		>div:nth-child(2) {
			display: flex;
			gap: 10px;
		}
	}
	.useless-button {
		background-color: #bdbdbd;
		border: 1px solid #828282;
		padding: 4px;
	}
}
.header-search__input {
	flex: 1;
	font-size: 14px;
	border-color: $primary-color;
	padding: 7px;
}
.header-search__reset {
	align-self: center;
	position: absolute;
	right: 75px;
}
.header-search__button {
	font-size: 14px;
	font-weight: 700;
	padding: 0 10px;
	border-radius: 0 2px 2px 0;
}
.header-search__option {
	display: flex;
	align-items: center;
	label {
		font-size: 12px;
	}
}


.suggest {
  visibility: hidden; //hide
  display: flex;
  flex-direction: column;
  gap: 15px;
  position: absolute;
  z-index: 1001;
  top: 38px;
  left: 0;
  right: 0;
  background: #fff;
  padding: 20px;
  &__text {
    font-size: 14px;
    color: $tc2;
  }
  &__results {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  &__all {
    margin-top: 20px;
    color: $tc1;
    font-weight: 700;
  }
}
.suggest-result {
  &__link {
    text-decoration: none;
    font-size: 14px;
    &:hover {
      text-decoration: underline;
    }
  }
  &__info {
    font-size: 12px;
    display: flex;
    gap: 10px;
  }
  &__code {
    color: $tc2;
  }
  &__price {
    font-weight: 700;
  }
}
