

// to device file?
.price__item {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}


.device-basic {
	display: flex;
	margin-bottom: $gap-xl;
	@media(max-width: $bp4) {
		flex-direction: column;
	}
}
.gallery {
	overflow: hidden;
	@media(max-width: $bp4) {
		margin-bottom: $gap-xl;
	}
}
.fotorama__thumb-border {
	border-color: $primary-color !important;
	border-width: 2px !important;
}
.device-basic__column {
	&:first-child {
		flex: 0 0 280px;
		margin-right: 20px;
		@media(max-width: $bp4) {
			flex-basis: 100%;
			margin-right: 0;
		}
	}
}
.product {
	display: flex;
	flex-wrap: wrap;
	@media (max-width: $bp2) {
		flex-direction: column;
	}
}
.product-info {
	flex: 1;
	font-size: 14px;
	> div {
		margin-bottom: 40px;
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.product-subtitle {
	font-size: 16px;
	font-weight: 700;
	display: flex;
	align-items: center;
	margin-bottom: 20px;
	img {
		margin-right: 10px;
	}
}
.basic-params {
	&__item {
		display: flex;
		margin-bottom: 10px;
	}
	&__value {
		font-weight: 700;
		margin-left: 10px;
	}
}
.full-params {
	table {
		margin-bottom: 0;
	}
	tr {
		border-bottom: 1px solid #c4c4c4;
	}
	td {
		padding: 6px 10px;
		padding-left: 0;
		&:last-child {
			font-weight: 700;
			text-align: right;
			padding-right: 0;
		}
	}
}
.product-download {
	&__item {
		display: flex;
		align-items: flex-start;
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
		img {
			margin-right: 10px;
		}
	}
}
.product-description {
	p {
		line-height: 1.5;
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.product-order {
	flex: 0 0 auto;
	font-size: 14px;
	margin-left: 40px;
	@media (max-width: $bp2) {
		margin-left: 0;
		margin-bottom: 40px;
		order: -1;
	}
	> div {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__price {
		font-size: 24px;
		font-weight: 700;
	}
	&__status {
		margin-bottom: 10px !important;
	}
	&__order {
		form {
			height: 36px;
			display: flex;
		}
		input, button {
			font-size: 17px;
		}
		input {
			margin-right: 5px;
		}
		button {
			padding: 0 16px;
		}
	}
	&__add {
		a {
			color: #828282;
		}
	}
}
.tabs_product {
	flex-basis: 100%;
	margin-top: 40px;
	@media (max-width: $bp2) {
		.devices__wrap {
			grid-template-columns: repeat(3, 1fr);
		}
	}
	@media (max-width: $bp4) {
		.devices__wrap {
			grid-template-columns: repeat(2, 1fr);
		}
	}
}
.tabs__nav {
	display: flex;
	border-bottom: 1px solid #333;
	margin-bottom: $gap-md;
}
.tab {
	display: flex;
	align-items: center;
	padding: 10px;
	margin-bottom: -1px;
	border-radius: 4px 4px 0 0;
	border: 1px solid #bdbdbd;
	border-bottom-color: #333;
	color: #828282;
	text-decoration: none;
	margin-right: 5px;
	&:last-child {
		margin-right: 0;
	}
	&__qty {
		font-size: 12px;
		color: #fff;
		padding: 2px 5px;
		background-color: $primary-color;
		margin-left: 10px;
		border-radius: 2px;
		font-weight: 400;
		height: 20px;
		display: flex;
		align-items: center;
	}
}
.tab_active {
	background-color: #fff;
	border: 1px solid #333;
	border-bottom-color: #fff;
	color: #333;
	font-weight: 700;
}
.tab-container {
	p {
		line-height: 1.3;
	}
}
@media (max-width: $bp4) {
	.tabs__nav {
		flex-wrap: wrap;
		border-bottom: none;
	}
	.tab {
		border: 1px solid #bdbdbd;
		border-radius: 100px;
		padding: 5px 8px;
		margin-right: 5px;
		margin-bottom: 10px;
		&_active {
			border: 2px solid #333;
		}
		&__text {
			white-space: nowrap;
			font-size: 14px;
		}
		&__qty {
			font-size: 13px;
			border-radius: 100px;
			padding: 2px 6px;
		}
	}
}


hr {
	border-bottom: 1px solid $gray-1;
	margin-bottom: $gap-lg;
}


.extra-info {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: $gap-md;
	@media(max-width: $bp4) {
		grid-template-columns: 1fr;
	}
}
.extra-info__title {
	margin-bottom: $gap-md;
	font-weight: 700;
	font-size: 20px;
	span {
		margin-left: 4px;
	}
}
.extra-info__list {
	padding-left: 16px;
}
.extra-info__list-item {
	font-size: 13px;
	margin-bottom: $gap-md;
	position: relative;
	line-height: 1.3;
	&:last-child {
		margin-bottom: 0;
	}
	&:before {
		content:"";
		display: block;
		width: 6px;
		height: 6px;
		background-color: $secondary-color;
		border-radius: 50%;
		position: absolute;
		top: 3px;
		left: -16px;
	}
}
table.params {
	display: block;
	tr {
		display: flex;
		position: relative;
		border: none;
		&:before {
			content:"";
			display: block;
			width: 6px;
			height: 6px;
			background-color: $secondary-color;
			border-radius: 50%;
			position: absolute;
			top: 8px;
			left: 0;
		}
		td {
			padding: 4px 8px;
			&:first-child {
				color: $gray-2;
				padding-left: 16px;
				padding-right: 4px;
				&:after {
					content: ":";
				}
			}
			&:last-child {
				font-weight: normal;
			}
		}
	}
}
table.full-params {
	th {
		text-align-last: left;
		padding-top: 16px;
		padding-bottom: 8px;
	}
}
.device__info {
	table.params {
		tr {
			&:before {
				display: none;
			}
		}
		td {
			&:first-child {
				padding-left: 0;
			}
		}
	}
}
