.cart-big-link {
	font-size: 16px;
	text-decoration: none;
	display: inline-block;
	margin-bottom: 20px;
}
.cart-total {
	width: 390px;
	margin-left: auto;
}
.cart-items {
	&__head {
		td {
			font-size: 14px;
			font-weight: 700;
			color: #828282;
			padding-bottom: 10px;
		}
	}
}

.cart-item {
	font-size: 12px;
	> * {
		vertical-align: top;
		padding-bottom: 20px;
		padding-right: 20px;
		&:last-child {
			padding-right: 0;
		}
		> * {
			margin-bottom: 5px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__code {
		div:nth-child(2) {
			font-weight: 700;
		}
	}
	&__name {
		a {
			color: $tc1;
		}
		div {
			font-size: 10px;
			font-weight: 700;
			color: #828282;
			margin-top: 2px;
		}
	}
	&__price,
	&__sum {
		font-size: 14px;
		white-space: nowrap;
		font-weight: 700;
		span {
			font-size: 12px;
			font-weight: 400;
			white-space: normal;
		}
	}
	&__qty {
		> div {
			white-space: nowrap;
		}
		&_mobile {
			display: none;
		}
	}
	&__image {
		img {
			height: 50px;
			width: 50px;
			object-fit: contain;
		}
	}
}

.cart-total {
	font-size: 12px;
	&__text {
		div {
			margin-bottom: 5px;
		}
		span {
			color: #828282;
			margin-left: 10px;
			&.warning {
				color: $cRed;
				margin-left: 0;
			}
		}
	}
	&__sum {
		font-size: 14px;
		font-weight: 700;
		margin-bottom: 15px;
	}
	&__warning {
		color: $cRed;
		margin-bottom: 5px;
	}
	&__buttons {
		margin-top: 30px;
		.button {
			font-size: 17px;
			height: 36px;
			padding: 8px 16px;
		}
	}
}
.personal-order {
	display: table;
	width: 100%;
}
.personal {
	display: table-row;
	&:first-child {
		> * {
			&:before {
				display: block;
				margin-bottom: 10px;
			}
		}
	}
	> * {
		display: table-cell;
		padding-bottom: 20px;
		padding-right: 20px;
		&:last-child {
			padding-right: 0;
		}
		input {
			width: 100%;
		}
		&:before {
			display: none;
			font-weight: 700;
			font-size: 14px;
			margin-bottom: 5px;
		}
	}
	&__mark {
		&:before {
			content: 'Маркировка';
		}
	}
	&__type {
		&:before {
			content: 'Тип';
		}
	}
	&__case {
		&:before {
			content: 'Корпус';
		}
	}
	&__qty {
		width: 80px;
		&:before {
			content: 'Кол-во';
		}
	}
	&__link {
		&:before {
			content: 'Ссылка';
		}
	}
	&__more {
		&:before {
			content: 'Доп. информация';
		}
	}
}

.cart-confirm {
	&__subtitle {
		font-size: 16px;
		font-weight: 700;
		margin-bottom: 10px;
	}
	p {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	span {
		color: $cRed;
	}
}

@media (max-width: $bp4) {
	.cart-items {
		display: block;
		&__head {
			display: none;
		}
	}
	.cart-item {
		position: relative;
		display: grid;
		grid-gap: 10px;
		grid-template-columns: repeat(4, auto);
		grid-template: "i n n n"
									 "i q p s";
		border-bottom: 1px solid #e0e0e0;
		padding-bottom: 20px;
		margin-bottom: 20px;
		&_past {
			grid-template: "i n n n"
										 "q q p s";
		}
		&:first-child {
			padding-top: 0;
		}
		> * {
			display: block;
			padding: unset;
			margin: unset;
			&:before {
				display: block;
				font-size: 13px;
				color: #828282;
				font-weight: 400;
				margin-bottom: 5px;
			}
		}
		&__code {
			display: none;
		}
		&__image {
			grid-area: i;
			img {
				max-width: unset;
			}
		}
		&__name {
			grid-area: n;
			font-size: 14px;
			padding-right: 40px;
			> div {
				font-size: 14px;
				font-weight: 400;
				margin-top: 10px;
				margin-bottom: 0;
			}
		}
		&__qty {
			grid-area: q;
			&:before {
				content: 'Кол-во';
			}
			&_mobile {
				display: block;
			}
		}
		&__price {
			grid-area: p;
			font-size: 18px;
			&:before {
				content: 'Цена, руб./набор';
			}
		}
		&__sum {
			grid-area: s;
			font-size: 18px;
			&:before {
				content: 'Стоимость, руб.';
			}
		}
		&__close {
			position: absolute;
			top: 0;
			right: 0;
		}
	}
	.cart-total {
		width: unset;
	}
}
