.wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	.footer {
		margin-top: auto;
	}
}
.footer {
	background-color: $cGray;
	padding: 30px 0 15px;
	&__wrap {
		display: flex;
		flex-wrap: wrap;
	}
	&__column {
		margin-right: 50px;
		&:first-child {
			@media (min-width: $bp1) {
				flex: 0 0 387px;
			}
		}
		&:nth-child(2) {
			display: flex;
			flex-direction: column;
			align-items: center;
			gap: 15px;
		}
		&:nth-child(3) {
			margin-right: 0;
			display: flex;
			flex-direction: column;
			margin-left: auto;
		}
	}
	&__title {
		font-size: 16px;
		line-height: 1.2;
		font-weight: 700;
		margin-bottom: 15px;
	}
	&__text {
		font-size: 13px;
		line-height: 1.2;
	}
	&__phones {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-template-rows: repeat(3, auto);
		grid-gap: 10px 30px;
		.phone__number {
			font-size: 15px;
		}
	}
	&__phones-jur {
		grid-row: 3/span1;
		grid-column: 1/-1;
		font-size: 18px;
	}
}
.footer-payments {
	display: flex;
	gap: 8px;
	justify-content: center;
	flex-basis: 100%;
	margin-top: 20px;
}
.footer-social {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 10px;
	&__icons {
		display: flex;
		gap: 10px;
		margin-bottom: 15px;
	}
	a {
		font-size: 14px;
		text-decoration: none;
	}
}
.copyright {
	font-weight: 700;
	margin-bottom: 10px;
}
.forever {
	color: #828282;
	font-size: 12px;
	text-decoration: underline;
}

@media (max-width: $bp2) {
	.footer {
		&__wrap {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: 20px;
			> div {
				margin: unset !important;
				padding: unset;
				flex-direction: row !important;
				align-items: flex-start !important;
			}
		}
		&__social {
			order: 1;
			margin-right: 10px;
		}
		&__copyright {
			order: 0;
			margin-right: 10px;
		}
	}
	.forever {
		order: 2;
	}
}
@media (max-width: $bp3) {
 .footer {
	 &__wrap {
		 grid-template-columns: 1fr;
	 }
	 &__phones {
		 grid-template-columns: repeat(2, 1fr);
	 }
	 &__payments {
		 flex-wrap: wrap;
	 }
 }

}
