input {
	&[type=number] {
		width: 50px;
	}
}

input[type=checkbox] {
	margin-right: $gap-xs;
}

.input, select {
	line-height: 1;
	border: 1px solid $border-color;
	padding: 4px 6px;
}
.button {
	display: inline-block;
	white-space: nowrap;
	padding: 4px 6px;
	line-height: 1;
	font-size: 14px;
	border-radius: 2px;
	background-color: $primary-color;
	color: #fff;
	border: 1px solid $primary-color;
	&:hover {
		background-color: darken($primary-color, 10%);
		border-color: darken($primary-color, 10%);
	}
	&_secondary {
		background-color: #bdbdbd;
		color: $tc1;
		border: none;
		&:hover {
			background-color: #bdbdbd;
		}
	}
	&_disabled {
		color: #bdbdbd;
		background-color: #e0e0e0;
		border: none;
		&:hover {
			background-color: #e0e0e0;
		}
	}
	&_big {
		font-size: 17px;
		height: 36px;
		padding: 8px 16px;
	}
}

.form-text {
	margin-bottom: 20px;
}
.form {
	font-size: 14px;
	a {
		text-decoration: none;
	}
	.input,select {
		height: 25px;
		padding: 4px 6px;
		font-size: 14px;
		width: 400px;
	}
	&__buttons {
		display: flex;
		> * {
			margin-left: 5px;
			&:first-child {
				margin-left: 0;
			}
		}
	}
	&__item {
		margin-bottom: 20px;
	}
	&__label {
		font-size: 14px;
	}
	label {
		font-size: 12px;
		display: flex;
		align-items: center;
		input {
			margin-right: 7px;
		}
	}
}
.form-item {
	&__label {
		margin-bottom: 5px;
		display: flex;
		> div {
			&:nth-child(2) {
				margin-left: 20px;
			}
		}
		span {
			color: $cRed;
		}
	}
	&__label-set {
		margin-top: 10px;
		label {
			margin-bottom: 5px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}
