//view
$view-fz: 14px;
$veiw-border-color: $gray-1;



.view {
	font-size: $view-fz;
	.pagination {
		margin: 0;
	}
}
.view_section {
	display: flex;
	align-items: center;
	margin-bottom: 30px;
	border-bottom: 1px solid #e0e0e0;
	padding-bottom: 10px;
}
.view__column {
	margin-right: $gap-lg;
	display: flex;
	align-items: center;
	span {
		margin-right: 10px;
	}
	&:last-child {
		margin-left: auto;
		margin-right: 0;
		span:before {
			content: 'Кол-во на странице:';
		}
	}
}
.toggle-view {
	a {
		margin-right: 5px;
		&:last-child {
			margin-right: 0;
		}
	}
}

@media (max-width: $bp2) {
	.view__column {
		&.toggle-view {
			span {
				display: none;
			}
		}
		&:last-child {
			span:before {
				content: 'Показывать:';
			}

		}
		.pagination__label {
			display: none;
		}
	}
}
@media (max-width: $bp3) {
	.view__column {
		margin-right: 0;
		&.toggle-view {
			display: none;
		}
		span {
			display: none;
		}
		&:last-child {
			margin-left: auto;
			span {
				display: inline;
			}
		}
		.pagination {
			display: none;
		}
	}
}
@media (max-width: $bp6) {
	.view__column {
		&:last-child {
			span {
				display: none;
			}
		}
	}
}
