//filter
$filter-background: #F2F2F2;
$filter-action-background: $secondary-color;




.filter_section {
	margin-bottom: 30px;
}
.filter__wrap {
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
	}
}
.filter__item {
	padding: 5px;
	background-color: $filter-background;
	margin-right: 10px;
	margin-bottom: 15px;
	display: flex;
	flex-direction: column;
	&.active {
		background-color: #ACD7A5;
	}
}
.filter__title {
	font-size: 12px;
	padding-bottom: 10px;
	line-height: 1;
}
.filter__values {
	background-color: #fff;
	padding: 10px;
	overflow: hidden;
	overflow-y: auto;
	max-height: 120px;
	flex: 1;
}
.filter__control {
	margin-top: 5px;
	display: flex;
	justify-content: flex-start;
	label {
		margin-right: 10px;
		display: flex;
		align-items: baseline;
		input {
			margin-left: 3px;
		}
		&:last-child {
			margin-right: 0;
		}
	}
}
.filter__value {
	font-size: 12px;
	margin-bottom: $gap-sm;
	&:last-child {
		margin-bottom: 0;
	}
	label {
		display: flex;
	}
}
.filter__actions {
	flex-basis: 100%;
	button {
		margin-right: $gap-xs;
		&:last-child {
			margin-right: 0;
		}
	}
	@media(max-width: $bp4) {
		display: flex;
		flex-direction: column;
		button {
			flex: 0 0 100%;
			margin: 0 0 10px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}
.filter__button {
	padding-left: 12px;
}



.filtered {
	margin-bottom: 5px;
	&__wrap {
		display: flex;
		flex-wrap: wrap;
	}
	&__item {
		margin-right: 5px;
		margin-bottom: 5px;
		&:last-child {
			margin-right: 0;
		}
	}
}
.filtered-item {
	display: flex;
	background-color: #e0e0e0;
	border-radius: 2px;
	height: 24px;
	align-items: center;
	> * {
		padding: 0 7px
	}
	&__label {
		font-size: 14px;
		color: $tc1;
		padding-right: 0;
	}
	&__close {
		font-size: 12px;
		color: #828282;
		text-decoration: none;
		padding: 0 7px;
	}
}
